function toggleClassEl(el, className) {
  el.classList.toggle(className);
}

function getRowAndId(row) {
  const rowID = {};
  const checkbox = row.querySelector('[data-row]');
  const dataRowID = row.querySelector('[data-id]');

  if (dataRowID) {
    rowID.id = dataRowID.dataset.id;
  }

  if (checkbox) {
    rowID.row = checkbox.dataset.row;
  }
  return rowID;
}

function updateCheckBox(checkbox, check, dataRows) {
  const c = checkbox;
  c.checked = check;
  const indexRow = c.closest('tr');
  const { row, id } = getRowAndId(indexRow);
  if (check) {
    indexRow.classList.add('select');
    dataRows.addRow(row, id);
  } else {
    indexRow.classList.remove('select');
    dataRows.removeRow(row, id);
  }
}

function onCheckBoxAll(e, checkboxs, dataRows) {
  if (e.target.checked) {
    checkboxs.forEach((checkbox) => {
      updateCheckBox(checkbox, true, dataRows);
    });
  } else {
    checkboxs.forEach((checkbox) => {
      updateCheckBox(checkbox, false, dataRows);
    });
  }
}

function createOnCheckBoxALL(checkboxs, dataRows) {
  return (e) => {
    onCheckBoxAll(e, checkboxs, dataRows);
  };
}

function checkBoxAll(onCheckAll) {
  const checkAll = document.querySelector('#checkall');
  checkAll.addEventListener('change', onCheckAll);
}

function updateCheckAll(checkboxs, checkAll) {
  const currentCheckAll = checkAll;
  const result = checkboxs.filter((c) => c.checked === false);
  if (result.length === 0) {
    currentCheckAll.indeterminate = false;
    currentCheckAll.checked = true;
  }
}
function updateIndeterminate(checkboxs, checkAll) {
  const currentCheckAll = checkAll;
  const result = checkboxs.filter((c) => c.checked === true);
  if (result.length === 0) {
    currentCheckAll.indeterminate = false;
  }
}

const RowsCheck = () => {
  let rowsOnCheck = [];
  let dataID = [];
  return {
    addRow(row, id) {
      if (!rowsOnCheck.includes(row) && row) {
        rowsOnCheck.push(row);
      }
      if (!dataID.includes(id) && id) {
        dataID.push(id);
      }
    },
    removeRow(row, id) {
      rowsOnCheck = rowsOnCheck.filter((r) => r !== row);
      dataID = dataID.filter((i) => i !== id);
    },
    showRows() {
      return rowsOnCheck;
    },
    showID() {
      return dataID;
    },
  };
};

function onCheckBox(checkboxs, dataRows, e) {
  const checkAll = document.querySelector('#checkall');
  checkAll.indeterminate = true;
  const indexRow = e.closest('tr');
  toggleClassEl(indexRow, 'select');
  const { row, id } = getRowAndId(indexRow);
  if (e.checked) {
    dataRows.addRow(row, id);
  } else {
    dataRows.removeRow(row, id);
  }

  if (!e.checked && checkAll.checked) {
    checkAll.checked = false;
  } else if (!e.checked && checkAll.indeterminate) {
    updateIndeterminate(checkboxs, checkAll);
  } else {
    updateCheckAll(checkboxs, checkAll);
  }
}

function createOnCheckBox(checkboxs, dataRows) {
  return (e) => {
    onCheckBox(checkboxs, dataRows, e.target);
  };
}

function checkBoxChange(checkboxs, dataRows) {
  const onCheck = createOnCheckBox(checkboxs, dataRows);
  checkboxs.forEach((checkbox) => {
    checkbox.addEventListener('change', onCheck);
  });
}

function createCheckBox(datatable) {
  const checkbox = [];
  const activeRows = [...datatable.activeRows];
  activeRows.forEach((row) => {
    row.cells[0].classList.add('row-checkbox');
    checkbox.push(row.cells[0].firstElementChild.firstElementChild);
  });
  return checkbox;
}

function updateRow(rows, checkboxs, dataRows) {
  checkboxs.forEach((c) => {
    const checkbox = c;
    if (rows.includes(c.dataset.row)) {
      checkbox.checked = true;
      onCheckBox(checkboxs, dataRows, c);
    }
  });
}

function run(datatable) {
  const dataRows = RowsCheck();
  const dataTable = datatable;
  dataTable.dataCheckRows = dataRows;
  let checkbox = createCheckBox(dataTable);
  let onCheckAll = createOnCheckBoxALL(checkbox, dataRows);
  dataTable.on('datatable.init', () => {
    dataTable.header.firstElementChild.innerHTML = `<div class="ctn-checkbox"><input type="checkbox" name="" id="checkall" class="checkbox"></div>`;
    checkBoxAll(onCheckAll);
    checkBoxChange(checkbox, dataRows);
  });

  dataTable.on('datatable.sort', () => {
    const dataRowsCarret = dataRows.showRows();

    checkbox = createCheckBox(dataTable);
    const checkAll = document.querySelector('#checkall');
    checkAll.removeEventListener('change', onCheckAll, false);
    onCheckAll = createOnCheckBoxALL(checkbox, dataRows);
    updateRow(dataRowsCarret, checkbox, dataRows);
    checkBoxAll(onCheckAll);
    checkBoxChange(checkbox, dataRows);
  });
}

export default run;
