import { DataTable } from 'simple-datatables';
import run from './datatable';
import createModal from './Modal';
import post from './postData';
import Select from './Select';

function createDataTable(tableEl) {
  const dataTable = new DataTable(tableEl, {
    fixedColumns: false,
    perPageSelect: [5, 10, 50, 100],
    columns: [
      {
        select: 0,
        sortable: false,
        render: (data, cell, row) =>
          `<div class="ctn-checkbox"><input type="checkbox" class="checkbox" data-row=${row.dataIndex}></div>`,
      },
    ],
  });
  return dataTable;
}
const tableEl = document.querySelector('.table');
const rows = [...tableEl.tBodies[0].rows];
rows.forEach((row) => {
  row.insertCell(0);
});
const datatable = createDataTable(tableEl);

run(datatable);

const selectTable = Select([
  'Activate User',
  'Deactivate User',
  'Activate Staff',
  'Deactivate Staff',
  'Activate SuperUser',
  'Deactivate SuperUser',
]);

const dataTableTop = document.querySelector('.dataTable-top');

const contanerSel = document.createElement('div');
contanerSel.classList.add('dataTable-Action');
contanerSel.appendChild(selectTable);
dataTableTop.appendChild(contanerSel);

function postData() {
  const dataId = [...datatable.dataCheckRows.showID()];
  post('/admin_panel/update_user/', {
    id: dataId,
  });
}

const btnOpenModal = document.querySelector('#opt-activate-user');
btnOpenModal.addEventListener('click', () => {
  const lenofId = [...datatable.dataCheckRows.showID()].length;
  createModal({
    title: 'Activate User',
    content: `Are you sure you want to activate ${lenofId} accounts?`,
    callback: postData,
    idModal: 'modal',
  });
});
