const createModal = ({ title, content, callback, idModal }) => {
  const modal = document.getElementById(idModal);
  const btnClose = modal.querySelectorAll(`#${idModal}-close`);
  const modalTitle = modal.querySelector('.modal-title');
  const modalBodyText = modal.querySelector('.modal-body-text');

  const btnSave = modal.querySelector('#modal-save');

  modalTitle.innerText = title;
  modalBodyText.innerText = content;

  modal.style.display = 'block';
  document.body.style.overflow = 'initial';

  btnClose.forEach((element) => {
    element.addEventListener('click', () => {
      modal.style.display = 'none';
      document.body.style.overflow = 'initial';
    });
  });

  window.onclick = (event) => {
    if (event.target === modal) {
      modal.style.display = 'none';
      document.body.style.overflow = 'initial';
    }
  };

  btnSave.addEventListener('click', callback);
};

export default createModal;
